@charset "UTF-8";

// =======================================
// Font Settings
// =======================================

// 본문(paragraph) 사이즈
$body-fz: 16px;

// 본문(paragraph) 기본 컬러
$base-color: #171725;

// 기본 행간 비율
$line-height: 1.2;

// 본문 행간 비율
$base-line: $body-fz * 1.2;

// 적용 웹 폰트
$font-family-ko: 'SpoqaHanSansNeo', sans-serif;
$font-family-en: 'SpoqaHanSansNeo', Arial, sans-serif, serif;

//color
$c-red: #BC241C;
$c-red2: #F0142F;
$c-blue: #0557F8;

$c-gray: #D8D8D8;
$c-gray2: #CDCDCD;
$c-gray3: #AAAAAA;
$c-gray4: #707070;
$c-gray5: #545454;
$c-gray6: #393939;
$c-gray7: #171725;


$c-white: #fff;
$c-black: #000;